<template>
  <div class="stars">
    <div :key="index" class="star" ref="star" v-for="(item,index) in starsCount"></div>
  </div>
</template>

<script>
export default {
  name: 'stars.vue',
  data: function() {
    return {
      starsCount: 800, // 星星数量
      distance: 800 // 星星间距
    }
  },
  methods: {},
  mounted: function() {
    let _this = this
    let starArr = this.$refs.star
    // console.log(starArr)

    starArr.forEach(item => {
      var speed = 0.2 + Math.random() * 1
      var thisDistance = _this.distance + Math.random() * 300
      item.style.transformOrigin = `0 0 ${thisDistance}px` // 在transform变换后再进行一些平移
      item.style.transform = `translate3d(0,0,-${thisDistance}px) rotateY(${Math.random() *
        360}deg) rotateX(${Math.random() * -50}deg) scale(${speed},${speed})` // 平移+旋转+缩放
    })
  }
}
</script>

<style lang="less" scoped>
@keyframes rotate {
  0% {
    transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(0);
  }
  100% {
    transform: perspective(400px) rotateZ(20deg) rotateX(-40deg)
      rotateY(-360deg);
  }
}

.stars {
  transform: perspective(500px); /* 设置镜头到物体的距离 */
  transform-style: preserve-3d;
  position: absolute;
  perspective-origin: 50% 100%;
  left: 50%;
  bottom: 0;
  animation: rotate 90s infinite linear;
}

.star {
  width: 2px;
  height: 2px;
  background: #f7f7b8;
  position: absolute;
  top: 0;
  left: 0;
  backface-visibility: hidden;
}
</style>



