<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->

    <!-- 路由处理后渲染到此router-view处 -->
    <router-view />
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
body {
  /* radial-gradient 设置径向渐变色背景 */
  background: radial-gradient(
    200% 100% at bottom center,
    #f7f7b6,
    #e96f92,
    #1b2947
  );
  background: radial-gradient(
    220% 105% at top center,
    #1b2947 10%,
    #75517d 40%,
    #e96f92 65%,
    #f7f7b6
  );
  background-attachment: fixed; /* 设置背景图片不随页面滚动（相当于body来定位） */
  overflow: hidden;
}
</style>



